<template>
  <el-dialog title="" class="my-dialog"
            :visible.sync="dialogVisible"
            :close-on-click-modal="false"
            width="90%" center>

    <el-form ref="infoForm" :model="infoForm" label-width="130px" label-position="right">
      <el-alert v-if="is_checked==''" title=",, " type="warning" :closable="false" show-icon></el-alert>
      <el-form-item label="" prop="recdate">
        <!-- format, value-format -->
        <el-date-picker v-model="infoForm.recdate"
                        type="date" placeholder=""
                        format="yyyy  MM  dd "
                        value-format="yyyy-MM-dd">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="infoForm.serial"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="infoForm.owner"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="infoForm.hospital"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="infoForm.expert"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="infoForm.title" placeholder="">
          <el-option v-for="item in titleSelection" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="infoForm.name"></el-input>
      </el-form-item>
      <el-form-item label="()">
        <el-input v-model="infoForm.telephone"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="infoForm.address"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="infoForm.entrance"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker v-model="infoForm.birth"
                        type="month" placeholder=""
                        format="yyyy  MM "
                        value-format="yyyy-MM">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="infoForm.height" type="number" min="0">
            <template slot="append">cm</template>
        </el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="infoForm.weight" type="number" min="0">
          <template slot="append">kg</template>
        </el-input>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="infoForm.nation" placeholder="">
          <el-option v-for="item in nationSelection" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="infoForm.career" placeholder="">
          <el-option v-for="item in careerSelection" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="infoForm.culture" placeholder="">
          <el-option v-for="item in cultureSelection" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-checkbox v-for="(val, key) in specialCheckbox" :key="key" :label="val" v-model="infoForm[key]">
        </el-checkbox>
      </el-form-item>

      <el-form-item label="">
        <el-checkbox v-for="(val, key) in dietCheckbox" :key="key" :label="val" v-model="infoForm[key]">
        </el-checkbox>
        <el-input v-model="infoForm.yinshi_qita" placeholder=""></el-input>
      </el-form-item>

    </el-form>
    <span slot="footer">
        <el-button type="primary" :disabled="is_checked==''" @click="updateInfoForm"></el-button>
        <el-button @click="dialogVisible=false"></el-button>
    </span>

  </el-dialog>
</template>
<script>
import { apiUpdatePatientDataForm } from '@/api/api-prj001'
export default {
  name: "InfoForm",
  data() {
    return {
      dialogVisible: false,
      infoForm: {},
      titleSelection: ["","",""],
      nationSelection: ["","","","","","","","","","","","",
                        "","","","","","","","","","","","",
                        "","","","","","","","","","","",
                        "","","","","","","","","","",
                        "","","","","","","","","","","",""],
      careerSelection: ["","","","","","","","","","","",""],
      cultureSelection: ["","","","","","",""],
      specialCheckbox: {"special_gaowen":"","special_diwen":"","special_yeban":"","special_zao":"","special_fu":"","special_hua":"","special_ju":"","special_qi":"","special_kong":"","special_wu":""},
      dietCheckbox:    {"yinshi_wuteshu":"","yinshi_sushi":"","yinshi_suan":"","yinshi_xian":"","yinshi_xinla":"","yinshi_you":"","yinshi_shengleng":"","yinshi_cafei":""},
      exist: true,
      formName:'',
      is_checked:''
    }
  },
  computed:{
  },
  methods: {
    showmsg(message){
      console.log(message)
    },
    updateInfoForm () {
      apiUpdatePatientDataForm({formData:this.infoForm,formName:this.formName})
      .then((res)=> {
        this.$message({message: '',type: 'success'})
        this.dialogVisible = false
        this.$parent.getPatients()
      })
      .catch(
        // this.$message({message: '',type: 'error'})
      )
    }
  },
  created() {
      this.$on("openEvent", (data)=>{
        console.log('',data)
        this.dialogVisible = true
        this.exist = data.exist
        this.formName = data.formName
        this.is_checked = data.is_checked
        if (!data.exist) {
          //
          this.infoForm.info = data.formData.info
        } else {
          //()
          this.infoForm = data.formData
        }
      });
    }
};
</script>



