<template>
  <el-dialog title="" class="my-dialog"
            :visible.sync="dialogVisible"
            :close-on-click-modal="false" width="70%" center>

    <el-form ref="patientInfo" :model="patientInfo" :rules="rules" label-width="130px" label-position="left">

      <el-form-item label="" prop="name">
        <el-input v-model="patientInfo.name"></el-input>
      </el-form-item>

      <el-form-item label="" prop="hospital">
        <el-input v-model="patientInfo.hospital"></el-input>
      </el-form-item>

      <el-form-item label="" prop="birth">
        <!-- format, value-format -->
        <el-date-picker v-model="patientInfo.birth"
                        type="month" placeholder=""
                        format="yyyy  MM "
                        value-format="yyyy-MM">
        </el-date-picker>
      </el-form-item>

      <el-form-item label="" prop="phone">
        <el-input v-model="patientInfo.phone"></el-input>
      </el-form-item>

      <el-form-item label="	" prop="expert">
        <el-input v-model="patientInfo.expert"></el-input>
      </el-form-item>

      <el-form-item label="	" prop="title">
        <el-input v-model="patientInfo.title"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="patientInfo.nation" placeholder="">
          <el-option v-for="item in nationSelection" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" prop="career">
        <el-select v-model="patientInfo.career" placeholder="">
          <el-option v-for="item in careerSelection" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>

    </el-form>
      <span slot="footer">
        <el-button type="primary" @click="addPatient"></el-button>
        <el-button @click="dialogVisible=false"></el-button>
      </span>
  </el-dialog>
</template>

<script>
import { apiAddPatient } from '@/api/api-prj001'
export default {
    name:'AddPatient',
    data () {
      return {
        dialogVisible: false,
        patientInfo: {name:'',
                      hospital:'',
                      birth:'',
                      telephone:'13110983476',
                      expert:'',
                      title:'',
                      nation:'',
                      career:''},
        nationSelection: ["","","","","","","","","","","","",
                        "","","","","","","","","","","","",
                        "","","","","","","","","","","",
                        "","","","","","","","","","",
                        "","","","","","","","","","","",""],
        careerSelection: ["","","","","","","","","","","",""],
        rules:{
          name: [
            {required: true, message: '', trigger: 'blur' }
          ],
          phone: [
            {required: true, pattern: /^1\d{10}$/, message: '11',trigger: 'blur'}
          ],
          hospital:[{required: true, message: ''}],
          career:  [{required: true, message: ''}]
        }
      }

    },
    methods: {
      addPatient () {
        this.$refs.patientInfo.validate( (valid) => {
          if (valid) {
            let para = {
              patientInfo: this.patientInfo
            }
            apiAddPatient(para)
            .then( (res)=> {
              this.$message({message: '',type: 'success'})
              this.dialogVisible = false
              this.$parent.getPatients()
              }
            )
            .catch()
          } else {
            this.$message({message: '',type: 'warning'})
            return false;
          }
        })

      }
    },
    created() {
      this.$on("addEvent", function(data) {
        this.dialogVisible = true;
      });
    }
}

</script>

<style lang="scss">

</style>



